import React from 'react'
// Ran into issues where fetch didn't exist. Something to do with "polyfill"?
import 'isomorphic-fetch'
import Head from 'next/head'


const GA_TRACKING_ID = 'UA-117176245-1';

const Page = (props) => (
  <div>
    <Head>
      <title>Get OSM planet files from S3</title>
      <meta name="viewport" content="initial-scale=1.0, width=device-width" key="viewport" />
      <meta name="description" content="Planet-on-S3 provides direct links to OpenStreetMap (OSM) generated planet OSM files, changesets, and history that are being publicly stored in AWS S3. You can easily download the latest available snapshot as well as any archived snapshots without additional credentials and tools. Download the planet faster and more reliably."/>
      <meta name="keywords" content="OpenStreetMap,OSM,planet,changesets,AWS,S3,URLs"/>
      <script async src="https://www.googletagmanager.com/gtag/js?id=UA-117176245-1"></script>
      <script
            dangerouslySetInnerHTML={{
              __html: `
                window.dataLayer = window.dataLayer || [];
                function gtag(){dataLayer.push(arguments)};
                gtag('js', new Date());
                gtag('config', '${GA_TRACKING_ID}');
              `,
            }}
          />
    </Head>

    <h2>OSM Planet Files on S3</h2>
    <p>OSM planet files are being publicly stored on S3.  This website provides direct links to the artifacts.  No need to bother with AWS accounts and cli tools</p>
    {
      props.Contents.map((content) => (
        <div key={content.Key}>
          <a href={'https://osm-pds.s3.amazonaws.com/'.concat(content.Key)}>{content.Key}</a>
        </div>
      ))
    }
  </div>
)

Page.getInitialProps = async ({ req }) => {
  // TODO: Need error handling. Maybe try-catch and redirect to error page?
  const res = await fetch('https://osm-pds.s3.amazonaws.com', {"method": "GET"})
  let resBody = await res.text()
  const parseString = require('xml2js').parseString
  parseString(resBody, function (err, result) {
    resBody = result.ListBucketResult
  });

  // Each content object looks like:
  // { Key: [ '2018/changesets-180101.orc' ],
  //   LastModified: [ '2018-01-04T04:51:16.000Z' ],
  //   ETag: [ '"edfa57dc6301276fa92089e018baa164-18"' ],
  //   Size: [ '1810754842' ],
  //   Owner: [ { ID: [Array], DisplayName: [Array] } ],
  //   StorageClass: [ 'STANDARD' ] }
  return resBody
}

export default Page



